import React, { useState, useEffect } from 'react';
import './index.scss';
import axios from 'axios';
import { Button } from 'antd';

export default () => {
    const state = 'cl1'
    const id = 'wx5224793b7dc7f7b7'
    const wxURL = 'https://open.weixin.qq.com/connect/oauth2/authorize'
    const [login] = useState(window.location.search.includes('code') && window.location.search.includes('state'))
    useEffect(() => {
        if (login) {
            axios.get(`http://api.woaidakele.cn/api/${state}/wechat`).then(res => {
                console.log(res)
                window.wx.config(res)
                // window.wx.ready(function(){
                //     window.wxInit = tre
                // })
                window.wx.error(function(res){
                    alert('error')
                })
            })
        } else {
            window.location.href = `${wxURL}?appid=${id}&redirect_uri=http://api.woaidakele.cn/api/${state}&response_type=code&scope=snsapi_userinfo&state=${state}#wechat_redirect`
        }
    }, [login])

    const IsLogin = props => {
        if (props.login) {
            return <h1>常来信箱</h1>
        } else {
            return <div className="login">
                <Button onClick={() => window.location.href = `${wxURL}?appid=${id}&redirect_uri=http://api.woaidakele.cn/api/${state}&response_type=code&scope=snsapi_userinfo&state=${state}#wechat_redirect`}>登陆</Button>
            </div>
        }
    }

    return <div className="PageFeedback">
        <IsLogin login={login} />
    </div>
};